<template>
  <div :id="chartId" :style="style" :lineData="lineData"></div>
</template>
<script>
// Â∞Ü echarts Â∞ÅË£ÖÊàêÁªÑ‰ª∂Ôºåprops Êé•Êî∂ÂèÇÊï∞ÔºådataÊòØÁà∂ÁªÑ‰ª∂ÂàÜÂèëÁªô echarts ÁöÑÊï∞ÊçÆÊ∫ê
export default {
  props: {
    width: {
      type: String,
      default: "450px"
    },
    height: {
      type: String,
      default: "300px"
    },
    lineData: {
      type: Object
    }
  },
  data() {
    return {
      myChart: null
    };
  },
  computed: {
    chartId: function() {
      return "id-" + Math.floor(Math.random()*100000);
    },
    style() {
      return {
        width: this.width,
        height: this.height
      };
    }
  },
  mounted() {
    this.renderLine();
    this.resizeChart();
  },
  beforeDestroy() {
    window.onresize = null;
  },
  methods: {
    resizeChart() {
      window.onresize = () => {
        this.myChart.resize();
      };
    },
    renderLine() {
      let lineData = this.lineData;
      let opts = lineData;

      this.myChart = this.$echarts.init(document.getElementById(this.chartId));
      this.myChart.setOption(opts);
    }
  },
  watch: {
    // Ê∑±Â∫¶ÁõëÂê¨‰º†ÂÖ•ÁöÑÊï∞ÊçÆÂèòÂåñ
    lineData: {
      handler(newVal, val) {
        this.data = newVal;
        this.myChart.dispose();
        this.renderLine();
      },
      deep: true
    }
  }
};
</script>
